  height: 90px;
  margin: -90px 0 0;
}

.social-box .chart-wrapper canvas {
  width: 100% !important;
  height: 90px !important;
}

.social-box ul {
  padding: 10px 0;
  list-style: none;
}

.social-box ul li {
  display: block;
  float: left;
  width: 50%;
