/* Main Styles */

.basix-container {
  display: table;
  min-height: 100vh;
  position: relative;
  width: 100%;
}

aside.left-panel {
  background: #272c33;
  display: table-cell;
  height: 100vh;
  min-height: 100%;
  padding: 0 15px;
  vertical-align: top;
  width: 280px;
  transition: width 0.3s ease;
}

.navbar {
  background: #272c33;
  border-radius: 0;
  border: none;
  display: block;
  margin: 0 0 100px;
  padding: 0;
}

.navbar .navbar-header {
  float: none;
  text-align: center;
  width: 100%;
}

.navbar .navbar-brand {
  border-bottom: 1px solid #4e4e52;
  color: #f1f2f7;
  font-family: "Open Sans";
  font-size: 22px;
  float: none;
  line-height: 50px;
  margin: 0;
  text-align: left;
  text-transform: capitalize;
  display: block;
  min-height: 69px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.navbar .navbar-brand span {
  font-weight: 600;
}

.navbar .navbar-brand img {
  max-width: 160px;
}

.navbar .navbar-brand.hidden {
  display: none;
}

.navbar .menu-title {
  border-bottom: 1px solid #4e4e52;
  color: #9496a1;
  clear: both;
  display: block;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 700;
  line-height: 50px;
  padding: 15px 0 0;
  text-transform: uppercase;
  width: 100%;
}

.navbar .navbar-nav {
  float: none;
  position: relative;
}

.navbar .navbar-nav li {
  width: 100%;
}

.navbar .navbar-nav li.active .menu-icon,
