  background: none;
  color: #c8c9ce;
  display: inline-block;
  font-family: "Open Sans";
  font-size: 14px;
  line-height: 30px;
  padding: 10px 0;
  position: relative;
  width: 100%;
}

.navbar .navbar-nav li > a:hover,
