  color: #fff;
}

.navbar .navbar-nav li > a .menu-icon {
  color: #8b939b;
  float: left;
  width: 30px;
  text-align: center;
  z-index: 9;
  line-height: 30px;
}

.navbar .navbar-nav li > a .menu-title-text {
  font-size: 14px;
}

